.more__backdrop, .location__backdrop, .ingr__backdrop, .franchise__backdrop, .buy__backdrop {
  z-index: 10;
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: rgba(0, 0, 0, .5);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden.more__backdrop, .is-hidden.location__backdrop, .is-hidden.ingr__backdrop, .is-hidden.franchise__backdrop, .is-hidden.buy__backdrop {
  opacity: 0;
