  transform: translate(-50%, -50%)scale(.1);
}

.modal-more, .modal-location, .modal-ingr, .modal-franchise {
  width: 280px;
  height: 90%;
  opacity: 1;
  background-color: #fcfcfc;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: scroll;
  transform: translate(-50%, -50%)scale(1);
}

.button-modal-more, .button-modal-location, .button-modal-ingr, .button-modal-franchise, .button-modal {
  background-color: rgba(0, 0, 0, 0);
